.map-h2 {
    text-transform: uppercase;
    font-size: 1rem;
    padding: 20px;
    padding-left: 10px;
    text-align: center;
  }
  
  .google-map {
    width: 100%;
    height: 60vh;
  }
  
  .pin {
    display: flex;
    /* align-items: center; */
    width: 50px;
    color: var(--main-blue);
  }
  
  .pin-icon {
    font-size: 0.8rem;
  }
  
  .pin-text {
    font-size: 0.8em;
  }
  
  @media screen and (min-width: 799px) {
    .google-map {
      height: 80vh;
    }
  
    .map-h2 {
      font-size: 0.8rem;
      font-weight: 400;
    }
  
    .pin {
      width: 15vw;
    }
  
    .pin-icon {
      font-size: 1.5vw;
      color: red !important;
    }
  }